<template>
<div @dblclick="$emit('toggle-reminder', task.id)"
 :class="[task.reminder ? 'reminder': '', 'task']">
    <h3> {{ task.text }} </h3>
    <p> {{ task.day  }}</p>
    <i @click="$emit('delete-task', task.id)" class="fas fa-times"></i>
</div>
</template>

<script>
    export default{
        name: 'Task',
        props: {
            task: Object,
        },
    }
</script>

<style scope>
.fas {
  color: red;
  position: absolute;
  top:10px;
  right:20px;
}

.task {
  position: relative;
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
}

.task.reminder {
  border-left: 5px solid green;
}

.task h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
</style>