<template>
    <header>
        <h1>{{ title }}</h1>
        <Button @btn-click="$emit('toggle-add-task')" 
        :title="showAddTask ? 'Close' : 'Add Task'" :color="showAddTask ? '#DC143C' : 'green'" />
    </header>
</template>

<script>

import Button from './Button.vue';

    export default{
        name: 'Header',
        props: {
            title: String,
            showAddTask: Boolean,
        },
        components:{
            Button
        },
        emits:['toggle-add-task'],
    }
</script>
<style scoped>
    header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
    }
</style>